// Ð¡ÑÐ¸Ð»Ð¸ Ð´Ð»Ñ ÐºÐ½Ð¾Ð¿Ð¾Ðº "Buy now", "Franchise", "Our Locations", "Read more", "Products" - Ð¾Ð´Ð¸Ð½Ð°ÐºÐ¾Ð²ÑÐµ Ð´Ð»Ñ Ð²ÑÐµÑ Ð²ÐµÑÑÐ¸Ð¹

.button {
  padding: 10px 32px;

  font-weight: 600;
  font-size: 14px;
  line-height: calc(17 / 14);

  color: var(--dark-btn-text);
  background-color: var(--default-btn);
  border: 2px solid transparent;
  border-radius: 10px;
  transition: color var(--main-animation),
    background-color var(--main-animation), transform var(--main-animation),
    box-shadow var(--main-animation);

  &:hover {
    background-color: var(--hover-btn);
    transform: translateY(-2px);
    box-shadow: 0px 6px 6px rgba(29, 72, 190, 0.15);
  }

  &:focus {
    color: var(--light-btn-text);
    background-color: var(--hover-btn);
    transform: translateY(-2px);
    box-shadow: 0px 6px 6px rgba(29, 72, 190, 0.15);
  }
}

// ÐÐ¾Ð´Ð¸ÑÐ¸ÐºÐ°ÑÐ¾Ñ Ð´Ð»Ñ Ð¿ÑÐ¾Ð·ÑÐ°ÑÐ½Ð¾Ð¹ ÐºÐ½Ð¾Ð¿ÐºÐ¸

.button--transparent {
  background-color: transparent;
  border: 2px solid var(--default-btn);

  transition: border var(--main-animation), color var(--main-animation),
    transform var(--main-animation), box-shadow var(--main-animation);

  &:hover {
    background-color: transparent;
    border: 2px solid var(--hover-btn);
    transform: translateY(-2px);
    box-shadow: 0px 6px 6px rgba(29, 72, 190, 0.15);
  }

  &:focus {
    outline: none;
    background-color: transparent;
    color: var(--light-btn-text);
    border: 2px solid var(--hover-btn);
    transform: translateY(-2px);
    box-shadow: 0px 6px 6px rgba(29, 72, 190, 0.15);
  }
}

