.button--products {
  background-color: transparent;
  border: 2px solid var(--default-btn);

  transition: border var(--main-animation), color var(--main-animation),
    transform var(--main-animation), box-shadow var(--main-animation);

  &:hover {
    background-color: transparent;
    border: 2px solid var(--hover-btn);
    transform: translateY(-2px);
    box-shadow: 0px 6px 6px rgba(29, 72, 190, 0.15);
  }

  &:focus {
    outline: none;
    background-color: transparent;
    color: var(--light-btn-text);
    border: 2px solid var(--hover-btn);
    transform: translateY(-2px);
    box-shadow: 0px 6px 6px rgba(29, 72, 190, 0.15);
  }

  @include large-max {
    padding: 7px 11px;

    font-size: 8px;
    line-height: calc(10 / 8);
  }
}

// ÐÐ¾Ð´Ð¸ÑÐ¸ÐºÐ°ÑÐ¾Ñ Ð´Ð»Ñ ÐºÐ½Ð¾Ð¿ÐºÐ¸ "How itâs made?"

.button--how {
  padding: 12px 22px;
  background-color: transparent;
  color: var(--default-btn);
  border: none;
  text-decoration: underline;

  transition: color var(--main-animation);

  &:hover {
    background-color: transparent;
    color: var(--hover-btn);
  }

  &:focus {
    background-color: transparent;
    color: var(--light-btn-text);
  }

  @include large-max {
    padding: 9px 10px;

    font-size: 8px;
    line-height: calc(10 / 8);
  }
}
